<template>
	<div id="foodList">
		<div id="foodListBox">
			<div id="foodListAll" v-proportion='0.4' v-for="(item,index) in foodResult" :key='index' @click="click(index)" v-show="index<num">
				<img id="foodListImg" :src="item.img1" v-proportion='1'/>
				<div id="foodListContent">
					<div id="foodListName">{{item.name}}</div>
					<div id="foodListInt">{{item.summary}}</div>
				</div>
			</div>
		</div>
		<div id="foodMore" @click="more" v-if="num<foodResult.length">加载更多</div>
		<div id="foodNone" @click="more" v-else>已全部加载完</div>
	</div>
</template>

<script>
	export default{
		name : 'foodList',
		data(){
			return{
					num:6
			}
		},
		props:['foodResult'],
		methods:{
			click(index){
				var that=this;
				this.$router.push({path:"foodDetail",query:{datas:that.foodResult[index]}})
			},
			more(){
				this.num+=6;
			}
		}
	}
</script>

<style>
	#foodList,#foodListAll,#foodListContent,#foodListBox{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	#foodList{
		width: 100%;
		height: 100%;
	}
	#foodListBox{
		flex-direction: row;
		justify-content: space-between;
		width: 72%;
		flex-wrap: wrap;
	}
	#foodListAll{
		width: 48%;
		background-color: #CEF6EC;
		margin: 1% 0 1% 0;
		flex-direction: row;
		justify-content: space-between;
		border: 1px solid #FAFAFA;
		border-bottom:2px solid #999999 ;
	}
	#foodListImg{
		width: 40%;
	}
	#foodListContent{
		width: 50%;
		height: 100%;
		justify-content: space-around;
		align-items: flex-start;
	}
	#foodListName{
		font-size: 25px;
		color: #FA5882;
	}
	#foodListInt{
		height: 50%;
		width: 100%;
		text-align: justify;
		text-justify: inter-ideograph;
		overflow: hidden;  
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	#foodMore{
		margin-top: 2%;
		color: #01DFA5;
	}
	#foodNone{
		margin-top: 2%;
		color: red;
	}
</style>
