<template>
	<div id='foreign'>
		<div id="foodHead" v-proportion="0.06">国外特色</div>
		<food-list :foodResult="foodResult"></food-list>
	</div>
</template>

<script>
	import foodList from './foodList.vue'
	import about from '../../../views/About.vue'
	var AV=about.AV;
	export default{
		name : 'foreign',
		components:{
			foodList
		},
		data(){
			return{
				foodResult:''
			}
		},
		mounted() {
			var query = new AV.Query('foods');
			var that=this;
			query.equalTo('country','中国');
			query.find().then(function(data){
				that.foodResult=JSON.parse(JSON.stringify(data));
			})
		}
	}
</script>

<style scoped="scoped">
	#foodHead{
		text-align: start;
		margin: 1% 0 1% 14%;
		width: 70%;
		background-color:#CEF6EC;
		color: white;
		text-shadow: 0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777, 0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333, 0px 8px 7px #001135;
		font-size: 30px;
		padding: 1% 0 0 2%;
	}
</style>
